:root {
	--container-width: 400px;
	--game-card-background: rgb(63, 122, 225);
	--game-card-cirle-background: rgb(255, 255, 255);
	--game-card-cross-color: rgb(0, 0, 0);
	--game-card-height: 400px;
	--game-font-size: 70px;
}

body {
	z-index: -1;
	font-family: Arial-Narrow;
}

.overlay {
	position: absolute;
	width: 100vw;
	height: 100vh;
	background-color: rgba(152, 150, 150, 0.8);
	z-index: 3;
}
.container {
	width: 400px;
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}
.container .game {
	display: flex;
	flex-direction: column;
	justify-content: center;
	flex: 12;
}

.container .github {
	flex: 1;
	margin-top: 20px;
}

.hide {
	display: none;
}

.app {
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.app h1 {
	margin-bottom: 10px;
	text-align: center;
}

.buttons {
	display: flex;
	justify-content: space-between;
	margin-top: 15px;
	padding: 0px 10px;
}

.button {
	font-size: 16px;
	padding: 8px 8px;
	background-color: #ffffff;
	border-radius: 5px;
	cursor: pointer;
	transition: 0.3s;
	border: 1px solid grey;
	user-select: none;
}

.button:hover {
	transform: scale(1.05);
}

.button:focus {
	outline: none;
}

.github a {
	text-decoration: none;
	color: var(--game-card-background);
}

.github a:visited {
	text-decoration: none;
}

.github-icon {
	margin-left: 5px;
	color: var(--game-card-background);
}

@media (max-width: 600px) {
	.container {
		width: 95vw;
	}
	.game-board {
		height: 95vw;
		width: 95vw;
	}
	.card {
		width: 85vw;
	}

	.small {
		height: 120px;
		width: 120px;
		pointer-events: none;
		margin: 20px 0px;
	}
}
